import { useEffect } from "react";
import { useState } from "react";
import { contactService } from "../../services/contact.service";
import Contact from "../Contact/Contact";
import { useNavigate } from "react-router-dom";
import { ConstRoutes } from "../../constants/routes";



export default function ContactList({ cat, filters, sort, title = 'All Contacts', count = Infinity }) {
    const [contacts, setContacts] = useState([]);
    const [filteredContacts, setFilteredContacts] = useState([]);
    const navigate = useNavigate()

    useEffect(() => {
        if (!contacts.length) loadContacts()
    }, [])

    useEffect(() => {
        loadContacts()
    }, [cat])

    useEffect(() => {
        if (filters?.recentAdded) {
            setFilteredContacts(filterNewContacts(contacts, filters.recentAdded))
        }

        cat && filters &&
            setFilteredContacts(
                contacts.filter((contact) => {
                    return Object.entries(filters).every(([key, value]) => {
                        if (contact[key]) return contact[key] === value
                    })
                })
            );
    }, [contacts, cat, filters]);

    useEffect(() => {
        if (sort === "newest") {
            setFilteredContacts((prev) =>
                [...contacts].sort((a, b) => {
                    if (!a.createdAt && !b.createdAt) {
                        return 0;
                    }
                    if (!a.createdAt) {
                        return 1;
                    }
                    if (!b.createdAt) {
                        return -1;
                    }
                    return new Date(b.createdAt) - new Date(a.createdAt);
                })
            );
        } else if (sort === "asc") {
            setFilteredContacts((prev) =>
                [...contacts].sort((a, b) =>
                    a.name?.toLowerCase().localeCompare(b.name?.toLowerCase())
                )
            );
        } else {
            setFilteredContacts((prev) =>
                [...contacts].sort((a, b) =>
                    b.name?.toLowerCase().localeCompare(a.name?.toLowerCase())
                )
            );
        }
    }, [contacts, sort]);

    const loadContacts = async () => {
        try {
            let data = []
            if (cat) {
                data = await contactService.queryByCategory(cat)
            }
            else data = await contactService.query()
            setContacts(data)
            setFilteredContacts(data)
        } catch (err) {
            console.log('err', err)
        }
    };

    function filterNewContacts(contacts, months = 1) {
        const currentDate = new Date();
        const oneMonthAgo = new Date();
        oneMonthAgo.setMonth(oneMonthAgo.getMonth() - months);
        return contacts.filter(contact => {
            const createdAtDate = new Date(contact.createdAt);
            return createdAtDate >= oneMonthAgo && createdAtDate <= currentDate;
        });
    }

    return (
        <div className="contact-list--container">
            <div className="contact-list__header">
                <h5 className="title">{title}</h5>
                {filters?.recentAdded > 0 && <span onClick={() => navigate(ConstRoutes.NEW_CONTACTS)}>view all</span>}
            </div>

            <div className="contact-list--wrapper">
                {filteredContacts.length > 0 &&
                    filteredContacts.slice(0, count).map((contact) => {
                        return (
                            <Contact key={contact._id} contact={contact} />
                        )
                    })}
            </div>
        </div>
    )
}
